<template>
  <div class="home">
    <sidebar></sidebar>
    <div class="content">
      <topbar></topbar>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import topbar from './topbar'
  import sidebar from './sidebar'

  export default {
    name: 'home',
    components: {
      topbar,
      sidebar
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  .home {
    height: 100%;
    display: flex;
    flex-direction: row;
    transition: all .5s;
  }

  .content {
    flex-grow: 1;
    background: #f6f6f6;
  }

  .right {
    display: flex;
    flex-direction: row-reverse;
    padding-bottom: 10px;
  }
  
  .tableBox {
    padding: 10px;
  }

  .right button{
    margin-left: 10px;
  }
</style>


