<template>
  <div class="login" @keydown.13="login">
    <background></background>
    <div id="loging-form">
      <div class="formBox">
        <div><img src="../../static/test.png" alt=""></div>
        <div class="user_input">
          <i class="fa fa-user"></i>
          <input id="_username" class="username" type="text" v-model="username">
        </div>
        <div class="msg">
          {{ msg }}
        </div>
        <div class="user_input">
          <i class="fa fa-lock"></i>
          <input id="_password" class="password" type="password" v-model="password">
        </div>
        <div class="user_submit" @click="login">
          登录
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import background from './background'
  import axiosHttp from '../util/request'
  import axios from 'axios'

  export default {
    name: 'login',
    data() {
      return {
        username: '',
        password: '',
        msg: ''
      }
    },
    components: {
      background,
    },
    methods: {
      login: function () {
        // 直接登录
        // sessionStorage.setItem('userstate', 'loginState')
        // this.$router.push('home')
        axios.post('/login/userlogin', {
            username: this.username,
            password: this.password
          })
          .then((res) => {
            this.msg = res.data
            let loginState = res.data
            if (loginState['msg'] == "success") {
              sessionStorage.setItem('userstate', loginState)
              this.$router.push('home')
            } else {
              // 登录失败处理

            }
          })
          .catch(function (err) {
            console.log(err)
          })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .login {
    height: 100%;
    -moz-user-select: none;
    -o-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
  }

  #loging-form {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .formBox {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .formBox div {
    margin: 10px;
  }

  .formBox input {
    background: transparent;
    border: none;
    outline: none;
    font-size: 16px;
    color: #fff;
    font-weight: 600;
  }

  .user_input {
    border-bottom: solid 2px #fff;
  }

  .user_input i {
    color: #fff;
    font-size: 22px;
    margin: 5px 0;
  }

  .user_input input {
    padding: 3px;
  }

  .user_submit {
    border: solid 2px #fff;
    padding: 5px 25px;
    color: #fff;
    cursor: pointer;
  }

  .msg{
    color: #fff;
  }
</style>


