<template>
  <div>
    <div id="box">
      <el-card class="box-card">
        <div>主机总数&emsp;{{ infos.numHost }}</div>
      </el-card>
      <el-card class="box-card">
        <div>运行主机数&emsp;{{ infos.numHost_used }}</div>
      </el-card>
    </div>
    <hostInfo v-for="host in hostInfos" :key="host.id" :host="host"></hostInfo>
  </div>
</template>
<script>
  import axios from 'axios'
  import hostInfo from './echarts/hostInfo'

  export default {
    name: 'dashboard',
    data() {
      return {
        mychart: {},
        hostInfos: [],
        infos: {
          numHost: 0,
          numHost_used: 0
        },
      }
    },
    methods: {

    },
    beforeCreate: function () {
      axios.get('/api/listHypervisors')
        .then((res) => {
          this.infos.numHost = res.data.hypervisorsInfo.length
          this.hostInfos = res.data.hypervisorsInfo
          for(let host of this.hostInfos){
            host.state == 'up' ? this.infos.numHost_used++ : false
          }
        })
    },
    components: {
      hostInfo
    }
  }
</script>

<style scoped>
  #box {
    background: #f6f6f6;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
  }

  .box-card {
    width: 200px;
    margin: 10px;
  }
</style>


