<template>
  <div class="tableBox">
    <el-row class="right">
      <!-- 批量操作 -->
      <deleteImage :images="multipleSelection"></deleteImage>
      <createImage></createImage>
    </el-row>
    <el-table ref="multipleTable" :data="tableData3" v-loading="table_loading" tooltip-effect="dark" style="width: 100%"
      @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column prop="name" label="镜像名称">
      </el-table-column>
      <el-table-column prop="visibility" label="是否可见">
      </el-table-column>
      <el-table-column prop="status" label="状态">
      </el-table-column>
      <el-table-column prop="size" label="大小">
      </el-table-column>
      <el-table-column prop="min_ram" label="最小内存">
      </el-table-column>
      <el-table-column prop="min_disk" label="最小磁盘">
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="120">
        <template slot-scope="scope">
          <el-dropdown trigger="click">
            <el-button size="mini">
              更多菜单<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item @click.native="updateImageDialog(scope.row.server_id)">编辑镜像</el-dropdown-item>
              <el-dropdown-item @click.native="deleteImageDialog(scope.row.server_id)">删除镜像</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import axios from 'axios'
  import createImage from './menu/image/createImage'
  import deleteImage from './menu/image/deleteImage'

  export default {
    data() {
      return {
        tableData3: [],
        multipleSelection: [],
        table_loading: true
      }
    },
    beforeCreate: function () {
      axios.get('/image/listImages')
        .then((res) => {
          this.tableData3 = res.data['imageInfo']
          this.table_loading = false
        })
    },
    methods: {
      updateImageDialog(){

      },
      deleteImageDialog(){

      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
    },
    components: {
      createImage,
      deleteImage
    }
  }
</script>

<style scoped>

</style>


