import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import test from '@/components/test'
import home from '@/components/Home'
import dashboard from '@/components/view/dashboard'
import instances from '@/components/view/instances'
import images from '@/components/view/images'
import flavor from '@/components/view/flavor'
import storage from '@/components/view/storage'
import network from '@/components/view/network'
import user from '@/components/view/user'
import three from '@/components/three/test'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/test',
      component: test,
      children: [
        {
          path: '/',
          component: test
        },
        {
          path: 'three',
          component: three
        }
      ]
    },
    {
      path: '/home',
      component: home,
      children: [
        {
          path: '',
          redirect: 'dashboard'
        },
        {
          path: 'dashboard',
          component: dashboard
        },
        {
          path: 'instances',
          component: instances
        },
        {
          path: 'flavor',
          component: flavor
        },
        {
          path: 'images',
          component: images
        },
        {
          path: 'storage',
          component: storage
        },
        {
          path: 'network',
          component: network
        },
        {
          path: 'user',
          component: user
        }
      ]
    }
  ]
})



