<template>
  <div>
    <el-button size="mini" @click="beforeCreate()">创建卷</el-button>
    <el-dialog title="创建卷" :visible.sync="dialogVisible" width="400px" :before-close="handleClose">
      <el-form>
        <el-form-item label="卷名称">
          <el-input v-model="Stora.name" size="mini" placeholder="请输入名称"></el-input>
        </el-form-item>
        <el-form-item label="大小(GiB)">
          <el-input-number v-model="Stora.size" controls-position="right" @change="handleChange" :min="1" :max="10" size="mini"></el-input-number>
        </el-form-item>
        <el-form-item>
          <el-select v-model="Stora.volume_type" placeholder="类型" size="mini">
            <el-option v-for="stora in Stora.types" :key="stora.id" :label="stora.name" :value="stora.id"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="create()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'createStorage',
    data() {
      return {
        dialogVisible: false,
        Stora: {
          types: [
            {
              name: 'volume-type',
              id: 'volume-type',
            },
            {
              name: 'iscsi',
              id: 'iscsi',
            }
          ]
        }
      }
    },
    methods: {
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      handleChange(value) {
        console.log(value);
      },
      beforeCreate(){
        this.dialogVisible = true
      },
      create(){
        this.dialogVisible = false
        axios.post('/store/createVolume',{
          name: this.Stora.name,
          size: this.Stora.size,
          volume_type: this.Stora.volume_type
        })
        .then(()=>{
          $emit('updateView')
        })
      }
    }
  }
</script>

<style>

</style>


