<template>
  <div class="createServer">
    <el-button size="mini" @click="beforeCreate()">创建虚拟机</el-button>
    <el-dialog title="提示" :visible.sync="dialogVisible" width="500px" :before-close="handleClose">
      <el-form ref="form" :model="form" label-width="100px">
        <el-form-item label="虚拟机名称">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="镜像">
          <el-select v-model="form.image_id" placeholder="选择镜像">
            <el-option v-for="image in infos.images" :key="image.id" :label="image.name" :value="image.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="配置">
          <el-select v-model="form.flavor_id" placeholder="选择配置">
            <el-option v-for="flavor of infos.flavors" :key="flavor.id" :label="flavor.name" :value="flavor.flavor_id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="网络">
          <el-checkbox-group v-model="form.networks">
            <el-checkbox v-for="network of infos.networks" :key="network.id" :label="network.name" :value="network.id"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="创建数量">
          <el-input-number size="mini" v-model="form.number"></el-input-number>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="createServer()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'
  import qs from 'qs'

  export default {
    name: 'createServer',
    data() {
      return {
        dialogVisible: false,
        form: {
          name: '',
          image_id: '',
          flavor_id: '',
          networks: [],
          number: ''
        },
        infos: {
          images: [],
          flavors: [],
          networks: []
        }
      };
    },
    methods: {
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      beforeCreate(){
        this.dialogVisible = true
        axios.all([getimage(), getflavor(), getnetwork()])
        .then(([image, flavor, network]) => {
          this.infos.images = image.data.imageInfo
          this.infos.flavors = flavor.data.flavorInfo
          this.infos.networks = network.data.networkInfo
        })
      },
      createServer() {
        this.dialogVisible = false
        axios.post('/api/createServer', {
            name: this.form.name,
            image_id: this.form.image_id,
            flavor_id: this.form.flavor_id,
            network_id: this.form.networks,
            number: this.form.number,
          })
          .then((res) => {
            this.form.name = ''
            this.form.image_id = ''
            this.form.flavor_id = ''
            this.form.networks = []
            this.form.number = ''
          })
      }
    }
  }

  function getimage() {
    return axios.get('/image/listImages')
  }

  function getflavor() {
    return axios.get('/api/listflavors')
  }

  function getnetwork() {
    return axios.get('/network/listnetworks')
  }
</script>

<style scoped>

</style>


