<template>
  <div class="sidebar">
    <!-- <el-radio-group v-model="isCollapse" style="margin-bottom: 20px;">
      <el-radio-button :label="false">展开</el-radio-button>
      <el-radio-button :label="true">收起</el-radio-button>
    </el-radio-group> -->
    <el-menu :default-active="this.$route.path" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse"
      background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
      <router-link to='dashboard' tag='div'>
        <el-menu-item index="/home/dashboard">
          <i class="el-icon-menu"></i>
          <span slot="title">dashboard</span>
        </el-menu-item>
      </router-link>

      <router-link to='instances'>
        <el-menu-item index="/home/instances">
          <i class="el-icon-menu"></i>
          <span slot="title">虚拟机</span>
        </el-menu-item>
      </router-link>

      <router-link to='images'>
        <el-menu-item index="/home/images">
          <i class="el-icon-menu"></i>
          <span slot="title">镜像</span>
        </el-menu-item>
      </router-link>

      <router-link to='flavor'>
        <el-menu-item index="/home/flavor">
          <i class="el-icon-menu"></i>
          <span slot="title">虚拟机配置</span>
        </el-menu-item>
      </router-link>

      <router-link to='storage'>
        <el-menu-item index="/home/storage">
          <i class="el-icon-menu"></i>
          <span slot="title">存储</span>
        </el-menu-item>
      </router-link>

      <router-link to='network'>
        <el-menu-item index="/home/network">
          <i class="el-icon-menu"></i>
          <span slot="title">网络</span>
        </el-menu-item>
      </router-link>

      <el-submenu index="/system/">
        <template slot="title">
          <i class="el-icon-menu"></i>
          <span>系统</span>
        </template>
        <router-link to="user">
          <el-menu-item index="/system/user">用户</el-menu-item>
        </router-link>
      </el-submenu>

    </el-menu>
  </div>
</template>

<script>
  export default {
    name: 'sidebar',
    data() {
      return {
        isCollapse: false
      };
    },
    methods: {
      handleOpen(key, keyPath) {
        
      },
      handleClose(key, keyPath) {
        
      }
    }
  }
</script>

<style scoped>
  .sidebar {
    height: 100%;
  }

  .sidebar a{
    text-decoration: none;
  }

  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;
    height: 100%;
  }
</style>


