function instance_states(value){
  switch (value) {
    case 'active':
      return '运行'
      break;
    case 'building':
      return '创建中'
      break;
    case 'deleted':
      return '删除'
      break;
    case 'error':
      return '错误'
      break;
    case 'hard_reboot':
      return '硬重启中'
      break;
    case 'password':
      return '加密'
      break;
    case 'paused':
      return '暂停'
      break;
    case 'reboot':
      return '重启'
      break;
    case 'rebuild':
      return '重建'
      break;
    case 'rescued':
      return '营救中'
      break;
    case 'resized':
      return '调整中'
      break;
    case 'revert_resize':
      return '恢复调整'
      break;
    case 'shutoff':
      return '关机'
      break;
    case 'soft_deleted':
      return '软删除'
      break;
    case 'stopped':
      return '停止'
      break;
    case 'suspended':
      return '暂停'
      break;
    case 'verify_resize':
      return '验证调整'
      break;
    case 'shelved_offloaded':
      return '强制搁置'
      break;

    default:
      return '未知'
      break;
  }
}

function power_states(value){
  switch (value) {
    case 0:
      return '无状态'
      break;
    case 1:
      return '运行中'
      break;
    case 3:
      return '暂停'
      break;
    case 4:
      return '关闭'
      break;
    case 6:
      return '撞坏'
      break;
    case 7:
      return '暂停的'
      break;
  }
}

function instance_address(values){
  let addrs = []
  for(let key in values){
    let value = values[key]
    for(let k in value){
      let ip = value[k]
      addrs.push(ip.addr)
    }
  }
  return addrs
}

function task_states(values) { 
  return values ? values : '无状态'
}

export default {
  instance_states,
  instance_address,
  power_states,
  task_states
}


