<template>
  <div>
    <el-button size="mini" @click="beforeCreate()">创建用户</el-button>
    <el-dialog title="创建用户" :visible.sync="dialogVisible" width="400px" :before-close="handleClose">
      <el-form label-width="120px">
        <el-form-item label="用户名">
          <el-input v-model="user.username" size="mini" placeholder="请输入用户名"></el-input>
        </el-form-item>
        <el-form-item label="用户密码">
          <el-input type="password" v-model="user.password" size="mini" placeholder="请输入用户密码"></el-input>
        </el-form-item>
        <el-form-item label="邮箱">
          <el-input v-model="user.email" size="mini" placeholder="请输入邮箱地址"></el-input>
        </el-form-item>
        <el-form-item label="用户组">
          <el-input v-model="user.type_name" size="mini" placeholder="请输入用户所在组"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="create">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'createUser',
    data() {
      return {
        dialogVisible: false,
        user: {

        }
      }
    },
    methods: {
      handleClose(done) {
        this.dialogVisible = false
        this.user = {
          
        }
      },
      handleChange(value) {
        
      },
      beforeCreate(){
        this.dialogVisible = true
      },
      create(){
        this.dialogVisible = false
        axios.post('/login/createuser',{
          username: this.user.username,
          email: this.user.email,
          password: this.user.password,
          type_name: this.user.type_name
        })
        .then(()=>{
          this.user = {}
          this.$emit('updateview')
        })
      }
    }
  }
</script>

<style>

</style>


