<template>
  <div>
    <el-button size="mini" @click="beforeCreate()">创建配置</el-button>
    <el-dialog title="创建配置" :visible.sync="dialogVisible" width="400px">
      <el-form label-width="120px">
        <el-form-item label="名称">
          <el-input v-model="flavor.name" size="mini"></el-input>
        </el-form-item>
        <el-form-item label="ID">
          <el-input v-model="flavor.id" size="mini" placeholder="auto"></el-input>
        </el-form-item>
        <el-form-item label="VCPU数量">
          <el-input-number v-model="flavor.vcpus" :min="0" size="mini"></el-input-number>
        </el-form-item>
        <el-form-item label="内存(MB)">
          <el-input-number v-model="flavor.ram" :min="0" size="mini"></el-input-number>
        </el-form-item>
        <el-form-item label="根磁盘(GB)">
          <el-input-number v-model="flavor.disk" :min="0" size="mini"></el-input-number>
        </el-form-item>
        <el-form-item label="临时磁盘(GB)">
          <el-input-number v-model="flavor.ephemeral" :min="0" size="mini"></el-input-number>
        </el-form-item>
        <el-form-item label="Swap磁盘(MB)">
          <el-input-number v-model="flavor.swap" :min="0" size="mini"></el-input-number>
        </el-form-item>
        <el-form-item label="RX/TX 因子">
          <el-input-number v-model="flavor.rxtx_factor" :min="0" size="mini"></el-input-number>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="create()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import axios from 'axios'

  export default {
    name: 'createStorage',
    data() {
      return {
        dialogVisible: false,
        flavor: {}
      }
    },
    methods: {
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      },
      handleChange(value) {
        console.log(value);
      },
      beforeCreate(){
        this.dialogVisible = true
      },
      create(){
        this.dialogVisible = false
        axios.post('/api/createFlavor',{
          name: this.flavor.name,
          id: this.flavor.id,
          vcpus: this.flavor.vcpus,
          ram: this.flavor.ram,
          disk: this.flavor.disk,
          ephemeral: this.flavor.ephemeral,
          swap: this.flavor.swap,
          rxtx_factor: this.flavor.rxtx_factor          
        })
        .then(()=>{
          this.$emit('updateView')
        })
      }
    }
  }
</script>

<style>

</style>


